<template>
    <div class="chooseAdressWarp">
        <header>
            <router-link to="confiemorder">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <section>
                <span>选择地址</span>
            </section>
        </header>
        <router-link to="/newAddress" class="add_icon_footer">
            <i class="el-icon-circle-plus-outline" style="color: #3190e8;font-size: 1.2rem"></i>
            <span>新增收货地址</span>
        </router-link>
        <section class="scroll_container">
            <section class="list_cotainer">
                <ul class="deliverable_address">
                    <li class="deliverable_address_li" v-for="(item,index) in userList" :key="item.id" @click="liBtn(index,item)">
                        <div class="i_style">
                            <i :class="index == dianji ? 'el-icon-circle-check' : 'el-icon-circle-check1' "></i>
                        </div>
                        <div class="address_detail_container">
                            <section class="address_section1">
                                <span>{{item.name}}</span>
                                <span>先生</span>
                                <span>{{item.phone}}</span>
                            </section>
                            <section class="address_section2">
                                <span style="background-color: rgb(76, 217, 100);">{{item.tag_type}}</span>
                                <p>{{item.address_detail}}</p>
                            </section>
                        </div>
                    </li>
                </ul>
            </section>
        </section>
    </div>

</template>

<script>
    export default {
        name: "ChooseAddress",
        data(){
            return {
                userList:[],//收货人信息
                dianji:100,
            }
        },
        created(){
            //收货地址
            this.userId = localStorage.getItem("user_id")
            this.axios.get("/v1/users/"+this.userId+"/addresses").then(res=>{
                console.log(res);
                this.userList = res;
            }).catch(err=>{})
        },
        methods:{
            liBtn(i,item){
                console.log(i)
                console.log(item)
                // localStorage.setItem('addressInfo',item.name)
                this.dianji = i;
                localStorage.setItem("addressID",i)
                if (!localStorage.getItem("addressInfo")){
                    //第一次存
                    let hisArr = [];
                    hisArr.push(item);
                    localStorage.setItem("addressInfo",JSON.stringify(hisArr));
                } else{
                    //之后存
                    let hisArr = JSON.parse(localStorage.getItem("addressInfo"));
                    let newArr = hisArr.filter(v=>{
                        return v.name == item.name;
                    });
                    if (!newArr.length){
                        hisArr.push(item);
                        localStorage.setItem("addressInfo",JSON.stringify(hisArr));
                    }
                }
                this.$router.push('/confiemOrder');
            }
        }
    }
</script>

<style scoped>
    .chooseAdressWarp{
        padding-top: 1.95rem;
        /*background-color: azure;*/
    }
    header {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>a{
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
        color: #fff;
    }
    header>section{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    header>section span{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }

    .add_icon_footer{
        text-decoration: none;
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        height: 2.5rem;
        background-color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 204;
    }
    .add_icon_footer span{
        font-size: .8rem;
        color: #3190e8;
        margin-left: .3rem;
    }

    .scroll_container{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding-top: 1.95rem;
        overflow-y: auto;
    }
    .list_cotainer{
        padding-bottom: 5rem;
    }
    .deliverable_address{
        background-color: #fff;
    }
    .deliverable_address_li{
        display: flex;
        align-items: center;
        border-bottom: .025rem solid #f5f5f5;
        padding: .7rem;
        line-height: 1rem;
    }
    .i_style{
        width: .8rem;
        height: .8rem;
        /*fill: #4cd964;*/
        margin-right: .4rem;
    }
    .el-icon-circle-check{
        font-size: .8rem;
        color: #3190e8;
        margin-right: .2rem;
    }
    .el-icon-circle-check1{
        color: #fff;
    }
    .address_detail_container{
        margin-left: .2rem;
    }
    .address_section1{
        font-size: .65rem;
        color: #333;
    }
    .address_section1>:nth-child(1){
        font-size: .8rem;
        font-weight: 700;
    }
    .address_section2{
        width: 100%;
        display: flex;
        align-items: center;
        margin-top: .2rem;
    }
    .address_section2 span{
        font-size: .5rem;
        color: #fff;
        border-radius: .15rem;
        /*background-color: #ff5722;*/
        height: .6rem;
        line-height: .6rem;
        padding: 0 .2rem;
        margin-right: .3rem;
    }
    .address_section2 p{
        font-size: .55rem;
        color: #777;
    }





</style>